<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}行事曆`" :routeTarget="{name: 'ScheduleBreak'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <div class="flex items-center justify-between">
        <font-awesome-icon icon="fa-solid fa-angle-left"  @click="preWeek" class="text-2xl text-primary"/>
        <div class="flex">
            <p>{{weekLst[0] |dateFmtROCMMDD}}</p>
            <p> ~ {{$dayjs(weekLst[6]).format('MM/DD')}}</p>
        </div>
        <font-awesome-icon icon="fa-solid fa-angle-right"  @click="nextWeek" class="text-2xl text-primary"/>
      </div>

      <div class="mt-2">
        <!-- head -->
        <div class="flex">
          <div class="w-[12.5%] border border-solid border-primary"></div>
          <div class="w-[12.5%] border border-solid border-primary" v-for="(item,index) in weekLst" :key="`week${index}`">
            <p>{{$dayjs(item).format('M/D') }}</p>
            <p>{{$dayjs(item).format('(dd)') }}</p>
          </div>

        </div>
        <div class="flex">
          <div class="w-[12.5%] border border-solid border-primary">
            整日
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[0],weekLst[0])" @keypress="toggleAllDate(BreakWrap[0],weekLst[0])">
              <C-Checkbox-Full :value="BreakWrap[0]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[1],weekLst[1])" @keypress="toggleAllDate(BreakWrap[1],weekLst[1])">
              <C-Checkbox-Full :value="BreakWrap[1]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[2],weekLst[2])" @keypress="toggleAllDate(BreakWrap[2],weekLst[2])">
              <C-Checkbox-Full :value="BreakWrap[2]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[3],weekLst[3])" @keypress="toggleAllDate(BreakWrap[3],weekLst[3])">
              <C-Checkbox-Full :value="BreakWrap[3]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[4],weekLst[4])" @keypress="toggleAllDate(BreakWrap[4],weekLst[4])">
              <C-Checkbox-Full :value="BreakWrap[4]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[5],weekLst[5])" @keypress="toggleAllDate(BreakWrap[5],weekLst[5])">
              <C-Checkbox-Full :value="BreakWrap[5]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
          <div class="w-[12.5%] border border-solid border-primary">
            <span @click="toggleAllDate(BreakWrap[6],weekLst[6])" @keypress="toggleAllDate(BreakWrap[6],weekLst[6])">
              <C-Checkbox-Full :value="BreakWrap[6]" :disabled="true" class="!w-[32px]"></C-Checkbox-Full>
            </span>
          </div>
        </div>
        <!-- body -->
        <div>
          <div class="flex" v-for="(item,index) in restructureScheduleTableWithTimeList" :key="`resultItem${index}`">
            <div class="w-[12.5%] border border-solid border-primary">{{item.time}}</div>
            <div @click="toggle(innerItem,index,innerIndex)" @keypress="toggle(innerItem,index,innerIndex)" :class="[{'bg-slate-500':!innerItem}]" class=" flex w-[12.5%] cursor-pointer items-center justify-center border border-solid border-primary" v-for="(innerItem,innerIndex) in item.date" :key="`innerItem${innerIndex}`">
              <font-awesome-icon v-if="innerItem && (innerItem.statusComment === '該時段排休' || innerItem.statusComment === '該時段公休' || innerItem.statusComment === '本日公休')" icon="fa-solid fa-ban" class=" cursor-pointer text-xl text-rejectText"/>
              <span v-else>{{innerItem.statusComment}}</span>
            </div>
          </div>
        </div>
      </div>

      <div>
        <p class="my-1 mt-4 text-left">固定公休時間 :</p>
        <div class="flex flex-wrap gap-2">
          <template v-if="Array.isArray(regularHolidayList.allDay)">
            <div v-for="(holiday, index) in regularHolidayList.allDay" :key="`allholiday${index}`" class=" flex items-center rounded-2xl border border-solid border-primary px-2 py-1 text-sm">
                <span >{{holiday}}<font-awesome-icon icon="fa-regular fa-xmark" @click="toggleRemove('remove', holiday, true)" class="text-red ml-2 text-rejectText"/></span>
            </div>
          </template>
          <template v-if="Array.isArray(regularHolidayList.specificTime)">
            <div v-for="(holiday, index) in regularHolidayList.specificTime" :key="`holiday${index}`" class=" flex items-center rounded-2xl border border-solid border-primary px-2 py-1 text-sm">
                <span >{{holiday.week}} {{holiday.startHour}}:{{holiday.startMinute > 10 ? holiday.startMinute: `0${holiday.startMinute}` }}~{{holiday.endHour}}:{{holiday.endMinute > 10 ? holiday.endMinute: `0${holiday.endMinute}` }}<font-awesome-icon icon="fa-regular fa-xmark" @click="toggleRemove('remove', holiday, false)" class="text-red ml-2 text-rejectText"/></span>
            </div>
          </template>
        </div>
      </div>

      <div class="mt-4">
        <div class="w-full cursor-pointer bg-second py-2 px-4 text-white" v-ripple="100" @click="toggleSys" @keypress="toggleSys">設定整日／固定公休時間</div>
      </div>

    </section>
    <C-Modal-Dialog v-model="dialogStatus">
      <p class="mb-4 px-4 text-center font-bold" @click="toggleSys" @keypress="toggleSys">設定整日 ／固定公休日期</p>
      <div class="mb-4 flex justify-evenly gap-4">
        <div
          class="h-9 cursor-pointer rounded-md bg-[#ccc] px-4 duration-300"
         :class="[{'bg-[#434343] text-white': isAll},]"
         @click="isAll = true"
         @keypress="isAll = true"
         >
          <p class=" leading-9">整日</p>
        </div>
        <div
          class="h-9 cursor-pointer rounded-md bg-[#ccc] px-4 duration-300"
          :class="[{'bg-[#434343] text-white':!isAll}]"
          @click="isAll = false"
          @keypress="isAll = false"
        >
          <p class=" leading-9">時段</p>
        </div>
      </div>
      <div v-if="isAll">
        <C-Select class="w-full" :list="timeRepeat" v-model="seletedTimeRepeat" :placeholder="'選擇固定公休星期'" item-text="label" item-value="value"></C-Select>
      </div>
      <div v-else>
          <C-Select class="w-full" :list="timeRepeat" v-model="unAllSeletedTimeRepeat" :placeholder="'選擇固定公休星期'" item-text="label" item-value="value"></C-Select>
          <div class="my-2 flex gap-2">
            <C-Select class="w-full" :list="currentSetAllDatTimeRangeList" v-model="startTime" :placeholder="'開始時間'"></C-Select>
            <C-Select class="w-full" :list="currentSetAllDatTimeRangeList" v-model="endTime" :placeholder="'結束時間'"></C-Select>
          </div>
      </div>

      <div class="my-4 flex w-full items-center justify-center">
        <div class="w-1/2 cursor-pointer rounded-xl bg-second py-1 px-5 text-white" v-ripple="100" @click="setRegularHoliday('add')" @keypress="setRegularHoliday('add')">確認</div>
      </div>

    </C-Modal-Dialog>
  </main>
</template>

<script>
import time from '@/assets/constant/time';
import { mapGetters, mapState } from 'vuex';

export default {
  name: 'order-edit',
  description: '行事曆的列表',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 行事曆',
  },
  data() {
    return {
      seletedBreak: {
        sunDate: false,
        monDate: false,
        tueDate: false,
        wedDate: false,
        thuDate: false,
        friDate: false,
        satDate: false,
      },
      seletedTimeRepeat: '',
      unAllSeletedTimeRepeat: '',
      timeList: time,
      designerBookingList: [],
      period: 0,
      timeRepeat: [
        { label: '星期日', value: 'SUNDAY' },
        { label: '星期一', value: 'MONDAY' },
        { label: '星期二', value: 'TUESDAY' },
        { label: '星期三', value: 'WEDNESDAY' },
        { label: '星期四', value: 'THURSDAY' },
        { label: '星期五', value: 'FRIDAY' },
        { label: '星期六', value: 'SATURDAY' },
      ],
      isAll: true,
      startTime: '',
      endTime: '',
      regularHolidayList: [],
      dialogStatus: false,
      seleted: false,
    };
  },
  mounted() {
    this.init();
  },
  activated() {
    this.init();
  },
  methods: {
    async init() {
      await Promise.all([this.getDesignerBookingTimesWithRange(), this.getRegularHoliday()]);
    },
    doRoute() {
      this.$router.push({ name: 'Punch' });
    },
    toggleRemove(type, holiday, isAllDay) {
      this.$swal.fire({
        title: ' ',
        html:
        `<div class="flex items-center justify-center my-10">
          <img src="/designer/assets/images/error.svg" alt="" class="inline-block mr-1" /><span>確定要刪除嗎？</span>
        </div>`,
        confirmButtonText: '確認刪除',
        showConfirmButton: true,
        showCloseButton: true,
      }).then((res) => {
        if (res.isConfirmed) {
          this.setRegularHoliday(type, holiday, isAllDay);
        }
      });
    },
    async setRegularHoliday(type, holiday, isAllDay) {
      const submitForm = {
        designerId: this.userInfo.user.id,
        week: [],
        weekSpecificTime: [],
      };
      if (type === 'remove') {
        if (isAllDay) {
          submitForm.week = this.regularHolidayList.allDay.filter((resultHoliday) => resultHoliday !== holiday);
          submitForm.weekSpecificTime = this.regularHolidayList.specificTime;
        } else {
          submitForm.week = this.regularHolidayList.allDay;
          const weekSpecificTime = JSON.parse(JSON.stringify(this.regularHolidayList.specificTime));
          const removeIndex = this.regularHolidayList.specificTime.findIndex((item) => item.week === holiday.week && +item.startHour === holiday.startHour && +item.startMinute === holiday.startMinute && +item.endHour === holiday.endHour && +item.endMinute === holiday.endMinute);
          if (removeIndex !== -1) {
            weekSpecificTime.splice(removeIndex, 1);
            submitForm.weekSpecificTime = weekSpecificTime;
          }
        }
      }
      if (type === 'add') {
        if (this.isAll) {
          let week = [];
          if (this.seletedTimeRepeat !== '') {
            week = this.regularHolidayList.allDay.concat(this.seletedTimeRepeat);
          } else {
            this.$swal.fire({
              icon: 'info',
              title: '請選擇星期',
              showConfirmButton: false,
              showCloseButton: true,
              timer: 3000,
              timerProgressBar: true,
            });
            return;
          }
          submitForm.week = [...new Set(week)];
          submitForm.weekSpecificTime = this.regularHolidayList.specificTime;
        } else {
          let weekSpecificTime = JSON.parse(JSON.stringify(this.regularHolidayList.specificTime));
          if (this.unAllSeletedTimeRepeat !== '' && this.startTime !== '' && this.endTime !== '') {
            const weekSpecificTimeTemp = {
              week: this.unAllSeletedTimeRepeat,
              startHour: +this.startTime.split(':')[0],
              startMinute: +this.startTime.split(':')[1],
              endHour: +this.endTime.split(':')[0],
              endMinute: +this.endTime.split(':')[1],
            };
            const status = weekSpecificTime.find((item) => item.week === weekSpecificTimeTemp.week && +item.startHour === weekSpecificTimeTemp.startHour && +item.startMinute === weekSpecificTimeTemp.startMinute && +item.endHour === weekSpecificTimeTemp.endHour && +item.endMinute === weekSpecificTimeTemp.endMinute);
            if (!status) {
              weekSpecificTime = weekSpecificTime.concat(weekSpecificTimeTemp);
            }

            submitForm.weekSpecificTime = weekSpecificTime;
            submitForm.week = this.regularHolidayList.allDay;
          } else {
            this.$swal.fire({
              icon: 'info',
              title: '請選擇星期與時間',
              showConfirmButton: false,
              showCloseButton: true,
              timer: 3000,
              timerProgressBar: true,
            });
            return;
          }
        }
      }
      const resp = await this.$api.setRegularHoliday(submitForm);
      const { data, errors } = resp.data;
      if (errors) return;
      console.log(data);
      this.seletedTimeRepeat = '';
      this.dialogStatus = false;
      this.$swal.fire({
        title: ' ',
        html:
        `<div class="flex items-center justify-center my-10">
          <img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>設定成功</span>
        </div>`,
        showConfirmButton: false,
        showCloseButton: true,
        customClass: {
          confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
        },
        timerProgressBar: true,
        timer: 2500,
      });
      this.getDesignerBookingTimesWithRange();
      this.getRegularHoliday();
    },
    async getDesignerBookingTimesWithRange() {
      const submitForm = {
        designerId: this.userInfo.user.id,
        storeId: this.currentMechantId,
        dateRange: {
          startDate: this.$dayjs(this.weekLst[0]).valueOf(),
          endDate: this.$dayjs(this.weekLst[6]).valueOf(),
        },
      };
      const resp = await this.$api.getDesignerBookingTimesWithRange(submitForm);
      const { data, errors } = resp.data;
      if (errors) return;
      this.designerBookingList = data.getDesignerBookingTimesWithRange;
    },
    toggleSys() {
      this.dialogStatus = !this.dialogStatus;
    },
    toggleAllDate(status, dateTime) {
      console.log(status);
      console.log(dateTime);
      this.$swal.fire({
        showCloseButton: true,
        html:
            `<div class="flex items-center justify-center my-10">
              <img src="/designer/assets/images/${status ? 'success' : 'error'}.svg" alt="" class="inline-block mr-1" /><span> ${status ? '開啟' : '關閉'}全日預約</span>
            </div>`,
        showConfirmButton: true,
        confirmButtonText: '確認',
        customClass: {
          confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
        },

      }).then(async (res) => {
        if (res.isConfirmed) {
          const submitForm = {
            designerId: this.userInfo.user.id,
            datetime: this.$dayjs(this.$dayjs(dateTime).format('YYYY-MM-DD')).valueOf(),
            appointable: status,
          };
          const resp = await this.$api.adminBookingTimeWholeDay(submitForm);
          const { errors } = resp.data;
          if (errors) return;
          await this.getDesignerBookingTimesWithRange();
        }
      });
    },
    toggle(innerItem) { // , index, innerIndex
      if (!innerItem) return;
      this.$swal.fire({
        showCloseButton: true,
        html:
            `<div class="flex items-center justify-center my-10">
              <img src="/designer/assets/images/${innerItem.statusComment === null ? 'error' : 'success'}.svg" alt="" class="inline-block mr-1" /><span>${innerItem.statusComment === null ? '關閉預約' : '開啟預約'}</span>
            </div>`,
        showConfirmButton: true,
        confirmButtonText: '確認',
        canecelButtonText: '取消',
        showCancelButton: true,
        customClass: {
          confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
        },

      }).then(async (res) => {
        if (res.isConfirmed) {
          // this.resultList[index].date[innerIndex] = innerItem === '' ? true : '';
          await this.$api.adminBookingTime({ designerId: this.userInfo.user.id, datetime: innerItem.time, appointable: innerItem.statusComment !== null });
          await this.getDesignerBookingTimesWithRange();
          // this.$forceUpdate();
        }
      });
    },
    // async doAdminBookingTime() {

    // },
    async getRegularHoliday() {
      const resp = await this.$api.getRegularHolidaySetting({ userId: this.userInfo.user.id });
      const { data, errors } = resp.data;
      if (errors) return;
      this.regularHolidayList = data.getRegularHolidaySetting;
    },
    preWeek() {
      this.period -= 7;
    },
    nextWeek() {
      this.period += 7;
    },
    setTimeList() {
      // this.designerBookingList.flatMap((designerBookingListItem)=> ())
    },
  },
  watch: {
    seletedBreak: {
      handler(value, valuenew) {
        console.log(value, valuenew);
      },
    },
    weekLst: {
      deep: true,
      handler() {
        this.getDesignerBookingTimesWithRange();
      },
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    weekLst() {
      return [
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD 00:00:00')).day(this.period),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 1),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 2),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 3),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 4),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 5),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD 23:59:59')).day(this.period + 6),
      ];
    },
    restructureScheduleTableWithTimeList() {
      let timeList = [];
      let uniqueTimeList = [];
      let restructureScheduleTableWithTimeList = [];
      timeList = this.designerBookingList.flatMap((designerBookingListItem) => (!designerBookingListItem.bookingTimes ? []
        : designerBookingListItem.bookingTimes.flatMap((bookingTimesItem) => (!bookingTimesItem ? [] : [this.$dayjs(bookingTimesItem.time).isValid() ? this.$dayjs(bookingTimesItem.time).format('HH:mm') : []]))));
      uniqueTimeList = [...new Set(timeList)];

      const [sun, mon, tue, wed, thu, fri, sat] = this.designerBookingList;

      restructureScheduleTableWithTimeList = uniqueTimeList.flatMap((HHmm) => {
        let [sunDate, monDate, tueDate, wedDate, thuDate, friDate, satDate] = [];
        sunDate = sun.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        monDate = mon.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        tueDate = tue.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        wedDate = wed.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        thuDate = thu.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        friDate = fri.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        satDate = sat.bookingTimes.find((item) => this.$dayjs(item.time).format('HH:mm') === HHmm);
        return [{
          time: HHmm,
          date: [sunDate, monDate, tueDate, wedDate, thuDate, friDate, satDate],
        }];
      });
      return restructureScheduleTableWithTimeList;
    },
    BreakWrap() {
      const BreakWrap = this.designerBookingList.flatMap((item) => ((Array.isArray(item.bookingTimes) && item.bookingTimes.length > 0)
        ? [item.bookingTimes.every((innerItem) => innerItem.statusComment !== null)]
        : [false]));
      return BreakWrap;
    },
    currentSetAllDatTimeRangeList() {
      let currentSetAllDatTimeRangeList = [];
      if (Array.isArray(this.restructureScheduleTableWithTimeList)) {
        currentSetAllDatTimeRangeList = this.restructureScheduleTableWithTimeList.map((item) => item.time);
      }

      return currentSetAllDatTimeRangeList;
    },
  },
};
</script>

<style lang="scss" scoped>
// *{outline: 1px solid red;}
</style>
